.resultsContainer{
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    overflow-y: scroll;
    align-items: center;
    justify-content: center;

    .smallPlayerContainer {
        background-color: rgb(10, 212, 212);
        height: 200px;
        width: 120px;
        margin: 16px 8px;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        border-radius: 7px;
    
        .infoContainer {
            height: 85%;
            width: 80%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
    
            .imgContainer {
                width: 100%;
                height: 70%;
                border-radius: 5px;
                box-shadow: 3px 3px 5px rgb(68, 66, 66);
    
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                    border-radius: 5px;
                }
            }
    
            .infoName {
                font-size: 13px;
                font-weight: 500;
                background-color: rgb(255, 208, 208);
                text-align: center;
                padding: 4px;
                border-radius: 5px;
                border: 2px solid rgb(105, 105, 255);
            }
        }

        &:hover {
            height: 208px;
            width: 128px;
            margin: 12px 4px;
        }
    }
}